import {createSelector} from "reselect";

export const outReqSelector = {

    getArray: (state) => { //   
        return state.outputRequests.outputRequestsArray
    },
    getArrayHeaders: (state) => {//  
        return state.outputRequests.outputRequestsArrayHeaders
    },
    getActiveHeader: (state) => {//     
        return state.outputRequests.activeOutReqHeader
    },
    getArrayFiltBtn: (state) => {//     
        return state.outputRequests.outReqArrayFiltBtn
    },
    getActiveFiltBtn: (state) => {//     
        return state.outputRequests.outReqActiveFiltBtn
    },
    getOutReqSearchFieldData: (state) => {//     
        return state.outputRequests.outReqSearchFieldData
    },
    getSortHeaderDirection: (state) => {//    
        return state.outputRequests.sortHeaderDirection
    },

}

let getArrayFilteredByBtns = createSelector(//    
    [
        outReqSelector.getArray, //      
        outReqSelector.getActiveFiltBtn, //     switch/case
        outReqSelector.getArrayFiltBtn //      switch/case
    ],
    (outputRequestsArray, outReqActiveFiltBtn, outReqArrayFiltBtn) => {// ,  return  
        let commonPart2 = (condition) => outputRequestsArray.filter(condition) //   
        switch (outReqActiveFiltBtn) {
            case outReqArrayFiltBtn[1]: //     " "
                return commonPart2((item) => item.responses !== true)
            case outReqArrayFiltBtn[2]: //     " "
                return commonPart2((item) => item.responses === true)
            case outReqArrayFiltBtn[4]: //     ""
                return commonPart2((item) => item.archived === true)
            case outReqArrayFiltBtn[3]: //     "" -     
                let pattern = /(\d{2})\.(\d{2})\.(\d{4})/;
                let outputRequestsArray1 = outputRequestsArray.filter((item) => {
                        let dateItem = new Date(item.dataList.date.replace(pattern, "$3-$2-$1")); //       ,  new Date
                        let time = new Date().getTime() - 86400000 * 365; //     1970  - 1 
                        let dateOld = new Date(time); //    (1    )
                        return dateItem < dateOld //     
                    }
                )
                return outputRequestsArray1;
            default: //  
                return outputRequestsArray; //     
        }
    })

export let getArrayFilteredBySearchFieldAndButtons = createSelector( //        
    [
        getArrayFilteredByBtns, //     
        outReqSelector.getOutReqSearchFieldData, //     
    ],
    (outputRequestsArray, outReqSearchFieldData) => {// ,  return  
        let outputRequestsArray2 = outputRequestsArray.filter((item) =>
            item.dataList.name.toLowerCase().includes(outReqSearchFieldData.toLowerCase()))
        //              
        return outputRequestsArray2;
    })

export let getArrFiltSearchBtnsSortHeader = createSelector( //          
    [
        getArrayFilteredBySearchFieldAndButtons, //        
        outReqSelector.getActiveHeader, //   
        outReqSelector.getSortHeaderDirection, //   
        outReqSelector.getArrayHeaders,//    
    ],
    (outputRequestsArray, activeOutReqHeader, sortHeaderDirection, outputRequestsArrayHeaders) => {
        // ,  return  
        let commonPart1; //   
        let commonPart4 = (commonPart1, a, b) => {
            // parseInt -   
            // dataList... -      
            // .replace(/\s/g, "") -  
            let partA = commonPart1(a); //      
            let partB = commonPart1(b);
            let compareResult = sortHeaderDirection //  / 
                ? (partA < partB) ? 1 : -1 //  
                : (partA > partB) ? 1 : -1 //  
            return compareResult //  1  -1  
        }
        let commonPart5 = (commonPart1) => {
            let outputRequestsArray3 = outputRequestsArray.sort(//   
                (a, b) => {   //  
                    return commonPart4(commonPart1, a, b) //      
                });
            return outputRequestsArray3;
        }
        switch (activeOutReqHeader) {
            case outputRequestsArrayHeaders[0]: //    ""
                let pattern = /(\d{2})\.(\d{2})\.(\d{4})/; //    
                commonPart1 = (ab) => new Date(ab.dataList.date.replace(pattern, "$3-$2-$1"))
                return commonPart5 (commonPart1) //      
            case outputRequestsArrayHeaders[1]: //    " "
                commonPart1 = (ab) => ab.dataList.name //   
                return commonPart5 (commonPart1)
            case outputRequestsArrayHeaders[2]: //    "-"
                commonPart1 = (ab) => parseInt(ab.dataList.qty.replace(/\s/g, ""))//      
                return commonPart5 (commonPart1)
            case outputRequestsArrayHeaders[3]: //    ""
                commonPart1 = (ab) => parseInt(ab.dataList.cost.replace(/\s/g, ""))//      
                return commonPart5 (commonPart1)
            case outputRequestsArrayHeaders[4]: //    " "
                commonPart1 = (ab) => ab.responses //   
                return commonPart5 (commonPart1)
            default: //  
                return outputRequestsArray; //     
        }
    })

