import {connect} from "react-redux";
import React from "react";
import styles from "./OutReqContainer.module.scss";
import OutputRequests from "./OutputRequsts/OutputRequests";
import {
    markAsArchivedAC,
    removeOutReq,
    setActiveFiltBtnAC,
    setActiveHeaderAC,
    setOutReqSearchFieldDataAC, setSortHeaderDirectionAC
} from "../../store/reducers/output-requests";
import preloader from "../../assets/media/icons/Spin-1s-64px.svg"
import {getArrFiltSearchBtnsSortHeader, outReqSelector} from "../../store/selectors/output-requests-selectors";

class OutReqContainer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            initialisedApp: false, //   
        };
    }

    //             
    removeOutputRequest = (idRequest) => { //    
        this.props.removeOutReq(idRequest)
    }
    markAsArchived = (idRequest) => { //    
       // alert("   ID  " + idRequest + "  ");
        this.props.markAsArchivedAC(idRequest)
    }
    repeatRequest = (idRequest) => { //   
        alert("  ID  " + idRequest);

    }
    setOutReqFormData = (outReqSearchFieldData) => { //       
        if (!outReqSearchFieldData) {
            outReqSearchFieldData = ""
        }
        this.props.setOutReqSearchFieldDataAC(outReqSearchFieldData)
    }
    setActiveHeadarer = (value) => { //   
        if (value===this.props.activeOutReqHeader) {
            this.props.setSortHeaderDirectionAC(!this.props.sortHeaderDirection)
            return
        }
        this.props.setSortHeaderDirectionAC(true)
        this.props.setActiveHeaderAC(value)
    }
    setActiveFiltBtn = (value) => { //    
        if (value) {
            this.props.setActiveFiltBtnAC(value)
        }
    }

    render() {
        if (!this.state.initialisedApp) { //     
            setTimeout(() => {
                this.setState({initialisedApp: true})
            }, 1000) // 
            return <img className={styles.preloader} src={preloader} alt=""/> //   
        }

        return <div className={styles.container}>
            <OutputRequests
                removeOutputRequest={this.removeOutputRequest} //    
                markAsArchived={this.markAsArchived} //   ,     
                repeatRequest={this.repeatRequest} //   -    
                outputRequestsArray={this.props.outputRequestsArray} //    
                outReqArrayHeaders={this.props.outReqArrayHeaders} //    
                setOutReqFormData={this.setOutReqFormData}//    
                activeOutReqHeader={this.props.activeOutReqHeader} //     
                setActiveHeadarer={this.setActiveHeadarer} //      
                outReqArrayFiltBtn={this.props.outReqArrayFiltBtn}//     
                outReqActiveFiltBtn={this.props.outReqActiveFiltBtn}//     
                setActiveFiltBtn={this.setActiveFiltBtn} //    
                sortHeaderDirection={this.props.sortHeaderDirection} //     
            />
        </div>
    }
}

let mapStateToProps = (state) => {
    return {
        outputRequestsArray: getArrFiltSearchBtnsSortHeader(state),
        //    (   ,      )
        outReqArrayHeaders: outReqSelector.getArrayHeaders(state), //  
        activeOutReqHeader: outReqSelector.getActiveHeader(state), //     
        outReqArrayFiltBtn: outReqSelector.getArrayFiltBtn(state), //     
        outReqActiveFiltBtn: outReqSelector.getActiveFiltBtn(state), //     
        outReqSearchFieldData: outReqSelector.getOutReqSearchFieldData(state), //     
        sortHeaderDirection: outReqSelector.getSortHeaderDirection(state), //    
    }
}

export default connect(mapStateToProps, {
    removeOutReq, setActiveHeaderAC, setActiveFiltBtnAC, setOutReqSearchFieldDataAC,
    setSortHeaderDirectionAC, markAsArchivedAC
})(OutReqContainer)