import React from "react";
import SingleOutReq from "./SingleOutputRequest/SingleOutReq";
import styles from "./../ChildComponents/SingleOutputRequest/SingleOutReq.module.css"
import sort1 from "../../../../assets/media/icons/sort1.jpg"
import sort2 from "../../../../assets/media/icons/sort2.jpg"


let RenderAllOutReq = ({
                           removeOutputRequest, markAsArchived, repeatRequest, outputRequestsArray,
                           outReqArrayHeaders, activeOutReqHeader, setActiveHeadarer, sortHeaderDirection
                       }) => {
    //     ,     
    let outputRequestsElements = outputRequestsArray.map((d) => //      map
        {
            return <SingleOutReq //      
                key={d.idRequest} idRequest={d.idRequest} dataList={d.dataList} responses={d.responses} archived={d.archived}
                removeOutputRequest={removeOutputRequest}
                markAsArchived={markAsArchived} //     
                repeatRequest={repeatRequest} //  

            />
        }
    );
    let RenderOutReqArrayHeaders = () => { //     

        let MapOutReqArrayHeaders = ({value}) => {
            return <span onClick={() => {
                setActiveHeadarer(value)
            }}>
                <span className={activeOutReqHeader === value //       BLL
                    ?styles.outRecActiveHeader //   
                    :styles.outRecInactiveHeader} //   
                >{value}</span>
                <span>{activeOutReqHeader === value //       BLL
                    ? <img className={styles.sortImgStyle} src={sortHeaderDirection?sort1:sort2} alt=""/> //   
                    : null}</span> {/*   */}
            </span>
        }

        return <div
            className={styles.outputRequestsHeadersCommon}
        >
            {outReqArrayHeaders.map((value) => { //    
                return <MapOutReqArrayHeaders key={value.toString()} value={value}/>
            })}
        </div>
    }


    return (<div>
        <div><RenderOutReqArrayHeaders/></div>{/* */}
        <div>
            {outputRequestsElements} {/*    outputRequestsArray */}
        </div>
    </div>)
}

export default RenderAllOutReq