const REMOVE_OUT_REQ = "myApp/output-requests/REMOVE_OUT_REQ"; //    outputRequest
const SET_ACTIVE_HEADER = "myApp/output-requests/SET_ACTIVE_HEADER"; //    outputRequest
const SET_ACTIVE_FILTER_BUTTON = "myApp/output-requests/SET_ACTIVE_FILTER_BUTTON"; //      
const SET_OUT_REC_SEARCH_FIELD_DATA = "myApp/output-requests/OUT_REC_SEARCH_FIELD_DATA"; //      
const SET_SORT_HEADER_DIRECTION = "myApp/output-requests/SET_SORT_HEADER_DIRECTION"; //    
const MARK_AS_ARCHIVED = "myApp/output-requests/MARK_AS_ARCHIVED"; //    

//outReqSearchFieldData
export let removeOutReq = (idRequest) => { //     outputRequest
    return {type: REMOVE_OUT_REQ, idRequest}
};
export let setActiveHeaderAC = (activeOutReqHeader) => { //       
    return {type: SET_ACTIVE_HEADER, activeOutReqHeader}
};
export let setActiveFiltBtnAC = (outReqActiveFiltBtn) => { //       
    return {type: SET_ACTIVE_FILTER_BUTTON, outReqActiveFiltBtn}
};
export let setOutReqSearchFieldDataAC = (outReqSearchFieldData) => { //       
    return {type: SET_OUT_REC_SEARCH_FIELD_DATA, outReqSearchFieldData}
};
export let setSortHeaderDirectionAC = (sortHeaderDirection) => { //      
    return {type: SET_SORT_HEADER_DIRECTION, sortHeaderDirection}
};
export let markAsArchivedAC = (idRequest) => { //    /  
    return {type: MARK_AS_ARCHIVED, idRequest}
};

const initialState = {// 
    outputRequestsArrayHeaders: [ //   
        "", " ", "-", "", ""
    ],
    activeOutReqHeader: "",
    sortHeaderDirection: true,
    outReqArrayFiltBtn: [ //   
        " ", " ", " ", "", ""
    ],
    outReqActiveFiltBtn: " ", //    -   
    outReqSearchFieldData: "", //   
    outputRequestsArray: [ //    
        {
            idRequest: 1,
            responses: true,
            archived: true,
            dataList: {
                date: "27.06.2022",
                name: "   ",
                qty: "10 000 .",
                cost: "15 000  - 220 000 "
            },
        },
        {
            idRequest: 2,
            responses: false,
            archived: false,
            dataList: {
                date: "27.06.2021",
                name: "  16",
                qty: "1 000 .",
                cost: "10 000  - 220 000 ",
            },
        },
        {
            idRequest: 3,
            responses: false,
            archived: false,
            dataList: {
                date: "27.06.2020",
                name: " Contec CMS 50D2",
                qty: "10 .",
                cost: "20 000  - 220 000 ",
            },
        },
        {
            idRequest: 4,
            responses: true,
            archived: false,
            dataList: {
                date: "27.06.2019",
                name: " ",
                qty: "100 .",
                cost: "5 000  - 220 000 ",
            },
        },
    ],
};

let outputRequestsReducer = (state = initialState, action) => { //   
    let stateCopy; //      
    switch (action.type) {
        case REMOVE_OUT_REQ: //    outputRequest
            stateCopy = {
                ...state, //   
                outputRequestsArray: [...state.outputRequestsArray.filter(f => f.idRequest !== action.idRequest)], //     
            }
            return stateCopy;
        case SET_ACTIVE_HEADER: //      
            stateCopy = {
                ...state, //   
                activeOutReqHeader: action.activeOutReqHeader, //    
            }
            return stateCopy;
        case SET_ACTIVE_FILTER_BUTTON: //      
            stateCopy = {
                ...state, //   
                outReqActiveFiltBtn: action.outReqActiveFiltBtn, //    
            }
            return stateCopy;
        case SET_OUT_REC_SEARCH_FIELD_DATA: //      
            stateCopy = {
                ...state, //   
                outReqSearchFieldData: action.outReqSearchFieldData, //    
            }
            return stateCopy;
        case SET_SORT_HEADER_DIRECTION: //     
            stateCopy = {
                ...state, //   
                sortHeaderDirection: action.sortHeaderDirection, //   
            }
            return stateCopy;
        case MARK_AS_ARCHIVED: //   /  
            stateCopy = {
                ...state, //   
                outputRequestsArray: [...state.outputRequestsArray.map(item => { //    
                    if (item.idRequest === action.idRequest) { //  ID    ID   
                        item.archived = !item.archived //    
                    }
                    return item //   
                })],
            }
            console.log(state, stateCopy);
            return stateCopy;
        default:
            return state; //     
    }
}

export default outputRequestsReducer;

